import React from "react";
import "./Card.css";

function Card(props) {
  return (
    <div
      data-id={props.id}
      data-parentid={props.parentId}
      className="s-card float-left"
      title={props.name}
      onClick={props.renderChild}
    >
      {!props.imagePath && (
        <span
          className="place-holder"
          data-id={props.id}
          data-parentid={props.parentId}
        >
          {props.gender === "male" && (
            <i
              data-id={props.id}
              data-parentid={props.parentId}
              className="fas fa-male thumb"
            />
          )}
          {props.gender !== "male" && (
            <i
              data-id={props.id}
              data-parentid={props.parentId}
              className="fas fa-female thumb"
            />
          )}
        </span>
      )}
      {props.imagePath && props.imagePath !== "" && (
        <span
          className="thub-holder"
          data-id={props.id}
          data-parentid={props.parentId}
        >
          <img
            data-id={props.id}
            data-parentid={props.parentId}
            src={props.imagePath}
            alt={props.name}
          />
        </span>
      )}
      <span data-id={props.id} data-parentid={props.parentId} className="name">
        {props.name}
      </span>
    </div>
  );
}

export default Card;
