import React, { Component } from "react";
import _ from "lodash";
import Card from "../Card/Card";
import familyData from "./khandelwal-data";

import "./MyTree.css";

class MyTree extends Component {
  constructor(props) {
    super(props);
    this.state = {
      familyData: familyData.DATA_SET,
      activeFamilyData: this.find(1, familyData.DATA_SET2),
      levelOne: false,
      activeParent: "Raghunath Prasad Jasoria",
      activeId: 1,
      activeParentId: null,
      breadcrumbData: [{ name: "Raghunath Prasad Jasoria", id: 1 }]
    };
    this.renderChildTree = this.renderChildTree.bind(this);
    this.renderFirstLevel = this.renderFirstLevel.bind(this);
  }
  componentDidMount = () => {};

  renderBreadCrumb() {
    let that = this;
    let data = that.state.breadcrumbData;
    var arr = [];
    _.forEach(data, function(value, key) {
      arr.push(
        <li key={key} className="breadcrumb-item">
          <a
            onClick={() => {
              that.renderFirstLevel("", value.id);
            }}
            href="/#"
          >
            {value.name}
          </a>
        </li>
      );
    });
    return arr;
  }

  find(id, items) {
    var i = 0,
      found;

    for (; i < items.length; i++) {
      if (items[i].id === id) {
        return items[i];
      } else if (_.isArray(items[i].children)) {
        found = this.find(id, items[i].children);
        if (found) {
          return found;
        }
      }
    }
  }

  findAll(id, items) {
    var i = 0,
      found,
      result = [];

    for (; i < items.length; i++) {
      if (items[i].id === id) {
        result.push(items[i]);
      } else if (_.isArray(items[i].children)) {
        found = this.findAll(id, items[i].children);
        if (found.length) {
          result = result.concat(found);
        }
      }
    }

    return result;
  }

  renderActive(data) {
    if (data)
      return (
        <Card
          name={data.name}
          gender={data.gender}
          id={data.id}
          renderChild={this.renderFirstLevel}
        />
      );
  }

  renderMyTree(data, cName) {
    let that = this;
    var arr = [];
    _.forEach(data, function(value, key) {
      arr.push(
        <div key={key} className={cName}>
          <hr className="line-connector" width="1" size="10" color="#4391e0" />
          <Card
            name={value.name}
            gender={value.gender}
            id={value.id}
            parentId={value.parentId}
            renderChild={that.renderFirstLevel}
          />
        </div>
      );
    });
    return arr;
  }

  renderMySpouse(data, cName) {
    let that = this;
    var arr = [];
    if (data)
      _.forEach(data.spouse, function(value, key) {
        arr.push(
          <div key={key} className={cName}>
            <hr
              className="line-connector"
              width="1"
              size="10"
              color="#4391e0"
            />
            <Card
              name={value.name}
              gender={value.gender}
              id={value.id}
              renderChild={that.renderFirstLevel}
            />
          </div>
        );
      });
    return arr;
  }

  renderFirstLevel(e, id) {
    let _targetId =
      (e && e.target && e.target.dataset && e.target.dataset.id) || id;
    let newDataSet = this.find(parseInt(_targetId), familyData.DATA_SET2);
    if (newDataSet) {
      this.setState({
        levelOne: true,
        activeParent: newDataSet && newDataSet.name,
        activeFamilyData: newDataSet,
        breadcrumbData: this.getBreadcrumbData(newDataSet, _targetId)
      });
    }
  }

  renderChildTree(id) {
    let firstLevel =
      this.state.activeFamilyData && this.state.activeFamilyData.children;
    if (firstLevel) return this.renderMyTree(firstLevel, "m-auto b-top");
  }

  getBreadcrumbData(newDataSet, _targetId) {
    let a_data_set = this.state.breadcrumbData;
    let b_data_set = _.find(a_data_set, ["name", newDataSet.name]);
    if (_.isObject(b_data_set)) {
      console.log("exists");
      return a_data_set;
    } else {
      console.log("insert new");
      let joined = this.state.breadcrumbData.concat({
        name: newDataSet.name,
        id: parseInt(_targetId)
      });
      return joined;
    }
  }

  render() {
    return (
      <div>
        <div>
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <a href="/#">
                  <i className="fas fa-home" />
                </a>
              </li>
              <li className="breadcrumb-item" aria-current="page">
                Family Tree
              </li>
              {this.renderBreadCrumb()}
            </ol>
          </nav>
        </div>

        <div className="card bg-light mb-3">
          <div className="card-header">
            Family tree chart of <b>{this.state.activeParent}</b>
          </div>
          <div className="card-body">
            <div className="card-text" style={{ textAlign: "center" }}>
              <div className="row parent-row">
                {this.renderActive(
                  this.state.activeFamilyData,
                  this.state.activeId
                )}
                {this.renderMySpouse(
                  this.state.activeFamilyData,
                  "m-auto",
                  this.state.activeId
                )}
              </div>
              {this.state.levelOne && (
                <div className="row next-row">{this.renderChildTree()}</div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default MyTree;
